.left-bar-wrapper, .rooms, .rooms > .row{
    height: 100%;
}
.top-selector{
    /*margin: 0 auto;*/
    /*height: 155px;*/
    padding-bottom: 20px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}
.type-select{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #363636;
    vertical-align: top;
}
.select-img i{
    line-height: 50px;
    text-align: center;
    width: 50px;
    height: 50px;
    display: block;
    color: #808080;
}
.select-img i:hover, .select-img i:focus{
    color: #00ffff;
}
.rooms-list{
    max-height: 100%;
    /*overflow-y: scroll;*/
    height: calc(100% - 65px);
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}
.room-select{
    padding: 0 5px;
    overflow: hidden;
    cursor: pointer;
    display: block;
    margin: auto;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: #ff6c00;
    color: #ffffff;
    line-height: 85px;
    text-align: center;
    margin-bottom: 10px;
    font-size: 20px;
    border: 4px ridge black;
    /*transition: 0.3s linear;*/
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.room-select:hover{
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    text-decoration: none;
}

.sidebar-ppl > div{
    height: 65px;

}
.sidebar-ppl>div>div, .sidebar-ppl p{
    margin: auto;
    text-align: center;
}
.sidebar-ppl p{
    color: #ff6c00;
}
/* Add a black background color to the top navigation */
.topnav {
    background-color: #333;
    overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
    background-color: #ddd;
    color: black;
}

/* Add an active class to highlight the current page */
.active {
    background-color: #4CAF50;
    color: white;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
    display: none;
}

/* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */
@media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
        float: right;
        display: block;
    }
    .topnav.responsive {position: relative;}
    .topnav.responsive a.icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}




